nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 16px;
    z-index: 2;
    transition: all 0.7s;
    transition-timing-function: ease-in;
    height: 30px;
}
.icon{
    object-fit: contain;
    position: fixed;
    left: 30px;
    cursor: pointer;
}
.avatar{
    object-fit: contain;
    position: fixed;
    right: 30px;
    cursor: pointer;
}
.bg-clr{
    background-color: black;
}