.banner{
    height: 600px;
    font-weight: 600;
}
.banner-contents{
    position: relative;
    top: 290px;
    margin-left: 8%;
    max-width: 500px;
}
button{
    cursor: pointer;
    padding: 10px 2rem;
    font-weight: 700;
    border: none;
    background-color: #e6e6e6;
    color: black;
    transition: all 0.2s;
    font-size: 16px;
    margin-top: 2px;
}
button:hover{
    background-color: rgb(51, 51, 51, 0.5);
    color: white;
}
p{
    margin: 6px 0;
}
.ageres{
    margin-left: 10px
}
.film-data{
    margin-top: 20px;
}
