#row_posters{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 20px;
}
#row_posters::-webkit-scrollbar{
    display: none;
}
.contents-on-img{
    margin: 0 35px;
}
.row_poster{
    width: 100%; 
    height: 250px;
    margin-right: 10px;
    transition: transform 300ms;
}

.row_poster:hover{
    transform: scale(1.09);
}
.rowposterLarge{
    height: 100px;
    width: 100%;
    padding: 25px 40px;
}

h2{
    margin-left: 30px;
}
.notVisible{
    visibility: hidden;
    position: relative;
    top: 100px;
    left: 2px;
}
.visibleName{
    position: relative;
    top: 0;
    right: 25px;
    
}
.visibleButton{
    position: relative;
    top: 120px;
    left: 2px;
    background-color: rgb(51, 51, 51, 0.5);
    color: white;
} 
.visibleButton:hover{
    background-color: rgb(209, 204, 204);
    color: black;
}
.rowposterLarge  .visibleName{
    position: relative;
    top: -28px;
    left: -60px;
    display: block;
    height: 25px;
}
.rowposterLarge .visibleButton{
    position: relative;
    top: 25px;
}