.banner{
    height: 500px;
    object-fit: contain;
    color: white;
}
.banner_contents{
    position: relative;
    top: 260px;
    margin-left: 8%;
}
.banner_button{
    cursor: pointer;
    padding: 9px 2rem;
    font-weight: 700;
    border: none;
    margin-left: 4px;
    background-color: #e6e6e6;
    color: black;
    transition: all 0.2s;    
    font-size: 16px;
}
.banner_button:hover{
    background-color: rgb(51, 51, 51, 0.5);
}
.filmdata{
    margin-top: 25px;
}
.movie_name{
    font-weight: 800;
    font-size: 40px;
    margin-bottom: 0.4rem;
}
.desc{
    margin-top: 10px;
    max-width: 550px;
    line-height: 1.2;
}